import { scaleSqrt, scaleLinear } from 'd3-scale';

const colorArr = ['#FF6B3B', '#FFC100', '#9FB40F', '#76523B', '#0E8E89',
    '#F383A2', '#247FEA', '#2BCB95', '#B1ABF4', '#1D42C2',
    '#D64BC0', '#2498D1', '#BBBDE6', '#4045B2', '#21A97A',
    '#FF745A', '#007E99', '#FFC328', '#B40F0F', '#4435FF',
    '#FF5CA2', '#BBE800', '#004988'
]

export const dataToSerialNodes = (jsonData) => {
    
    const symbolSizeArr = jsonData.nodes.map(d => d.symbolSize);
    const lineSizeArr = jsonData.edges.map(d => d.size);
    const maxSize = Math.max(...symbolSizeArr);
    const maxLineSize = Math.max(...lineSizeArr);
    const scale = scaleSqrt()
        .domain([0, maxSize])
        .range([1, 25]);

    const lineSizeScale = scaleLinear()
        .domain([0, maxLineSize])
        .range([0, 10])
        .clamp(true)

    const nodes = jsonData.nodes.map(d => {
        return {
            key: d.id,
            attributes: {
                key: d.id,
                label: d.name,
                id: d.id,
                cluster: d.category.toString(),
                x: d.x,
                y: d.y,
                size: scale(d.symbolSize),
                color: colorArr[d.category]
            }
        }
    })

    const edges = jsonData.edges.filter(d=>d.size>=100).map((d, index) => {
        return {
            key: "edges" + index,
            source: d.source,
            target: d.target,
            attributes: {
                size: lineSizeScale(d.size),
                // color:'rgba(255,255,255,0)'
                // type: 'KNOWS'
            }
        }
    })
    console.log(edges)
    return {
        attributes: {
            name: "My Graph"
        },
        nodes,
        edges,
        "options": { "type": "mixed", "multi": false, "allowSelfLoops": true }
    }

}